<template>
  <div class="logistics">
    <div class="goodsList">
      <div class="goodsItem flex" v-for="(item,index) in goodsList" :key="index">
        <div class="goodsItem-pic flex-1">
          <img :src="$utils.dealImg(item.pic)" />
        </div>
        <div class="flex-2">
          <div class="title">{{item.sort_name}}</div>
          <div class="parameter" v-if="false">1.5L 白色</div>
          <div class="price">
            ¥
            <span class="money">{{$route.query.shopType == '2'?item.sale_price.toFixed(2) + '+' + item.market_price:item.sale_price.toFixed(2)}}</span>
            <span class="num">×{{item.buy_count}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="logistics-mid" v-if="express_company">
      <div class="logistics-top flex">
        <img :src="express_company == '德邦'?require('../../assets/img/icon/debang.jpg'):express_company == '京东'?require('../../assets/img/icon/jd.png'):require('../../assets/img/icon/pt.png')" alt />
        <div class="flex-2">
          <div>
            快递公司:
            <span>{{express_company}}</span>
          </div>
          <div>
            快递单号:
            <span>{{express_no}}</span>
          </div>
        </div>
      </div>
      <div class="logistics-list" v-if="logisticsList.length">
        <div class="line"></div>
        <div class="circle"></div>
        <div class="point" v-if="logisticsList.length > 1"></div>
        <div class="logistics-item flex" v-for="(item,index) in logisticsList" :key="index" :style="(index == 0?'color:#eb5252;':'') + (index + 1 == logisticsList.length?'padding:0;height:20px;':'')">
          <div class="time">{{item.AcceptTime.substring(5,16)}}</div>
          <div class="text">{{item.AcceptStation}}</div>
        </div>
      </div>
      <div class="nothing" v-else>
        <div class="nothing-mid">
          <img src="../../assets/img/nodata.png" alt />
          <p>暂无物流信息</p>
        </div>
      </div>
    </div>
    <div class="nothing" v-else>
      <div class="nothing-mid">
        <img src="../../assets/img/nodata.png" alt />
        <p>暂无物流信息</p>
      </div>
    </div>
  </div>
</template>
<script>
// import { getLogisticsList } from "@/api";
export default {
  data() {
    return {
      goodsList: [],
      logisticsList: [],
      express_company: "",
      express_no: ""
    };
  },
  methods: {
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getLogisticsList({ order_id: this.$route.query.id }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.goodsList = res.data.detail;
          this.express_company = res.data.express_company || "";
          this.express_no = res.data.express_no || "";
          if(res.data.result.Traces){
            res.data.result.Traces.map(item => {
              this.logisticsList.unshift(item);
            });
          }
          
        }
      });
    }
  },
  mounted() {
    document.title = "物流详情";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.logistics {
  overflow: hidden;
 
  .flex {
    display: flex;
    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      position: relative;
    }
  }
  .goodsList {
    border-bottom: 1pt solid #efefef;
    padding: 0 10px;
    .goodsItem {
      height: 120px;
      overflow: hidden;
      margin: 20px 0;
      .goodsItem-pic {
        margin-right: 10px;
        img {
          width: 100%;
          height: 120px;
        }
      }
    }
    .title {
      font-size: 16px;
      line-height: 25px;
      max-height: 50px;
      text-overflow: -o-ellipsis-lastline;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .parameter {
      color: #999;
      font-size: 15px;
      line-height: 20px;
      margin: 10px 0;
    }
    .price {
      font-size: 13px;
      font-weight: 600;
      color: #333;
      flex: 2;
      position: absolute;
      width: 100%;
      bottom: 10px;
      .money {
        font-size: 16px;
      }
      .num {
        color: #999;
        margin-left: 10px;
      }
    }
  }
  .logistics-mid {
    padding:0 10px 40px;
    .flex {
      display: flex;
    }
    .logistics-top {
      align-items: center;
      border-bottom: 1pt solid #ebebeb;
      padding: 15px 0;
      img {
        width: 40px;
        margin-right: 15px;
        border-radius:50%;
      }
      .flex-2 {
        font-size: 15px;
        color: #999;
        line-height: 20px;
        span {
          color: #000;
        }
      }
    }
    .logistics-list {
      margin: 20px 0;
      position: relative;
      line-height: 20px;
      .line {
        position: absolute;
        width: 15px;
        height: 100%;
        background: #f2f2f2;
        border-radius: 15px;
        top: 0;
        left: 85px;
      }
      .circle {
        position: absolute;
        width: 15px;
        height: 20px;
        background: $base-color;
        border-radius: 15px;
        top: 0;
        left: 85px;
      }
      .point {
        position: absolute;
        width: 15px;
        height: 15px;
        border: 3px solid $base-color;
        background: #fff;
        box-sizing: border-box;
        border-radius: 15px;
        bottom: 0;
        left: 85px;
      }
      .logistics-item {
        font-size: 13px;
        line-height: 20px;
        color: #999;
        padding: 0 0 30px;
        .time {
          margin-right: 25px;
          width: 100px;
        }
        .text {
          flex: 2;
        }
      }
    }
  }
  .nothing {
    height: calc(100vh - 160px);
    position: relative;
    background: #fff;
    &-mid {
      position: absolute;
      top: 30%;
      text-align: center;
      width: 100%;
      p {
        color: #666;
        line-height: 30px;
        font-size: 15px;
      }
    }
  }
}
</style>
